import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TableSortLabel from "@mui/material/TableSortLabel";

const dispatchedEvents = [];

const SrsMuiTableSortLabel = function (props, ref) {
  return (
    <TableSortLabel {...props} theme={props.muiTheme}>
      <slot />
    </TableSortLabel>
  );
};

const styles = `
  /* Custom styles for the MuiTableSortLabel component */
`;

const props = [
  {
    name: "active",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "direction",
    defaultValue: "asc",
    type: "string",
  },
  {
    name: "hideSortIcon",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to TableSortLabel if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTableSortLabel),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
